.page {
  background-color: #EFEFEF;
}
.tabbar-wrap {
  height: 3.125rem;
  position: fixed;
  bottom: 0;
  /* 距离底部为0像素 */
  left: 0;
  /* 左侧与视口边界对齐 */
  width: 100%;
  /* 宽度等于视口宽度 */
  z-index: 9999;
}
.home-page {
  width: 100%;
  height: 100%;
  overflow: auto;
  background: transparent;
  color: #444;
}
.page-detail {
  background-color: #fff;
  padding-top: 0.625rem;
  padding-bottom: 0.625rem;
}
.page-detail .price-wrap {
  background-color: #FFF5E2;
  padding: 0.3125rem 0.9375rem;
}
.page-detail .price-wrap .price {
  padding-top: 0;
  padding-bottom: 0;
  line-height: 1.875rem;
  vertical-align: bottom;
  height: 1.9375rem;
  overflow: hidden;
}
.page-detail .price-wrap .price i.unit {
  color: #EE4D2D;
  vertical-align: bottom;
  padding-right: 0.3125rem;
  display: inline-block;
  overflow: hidden;
  height: 1.5625rem;
}
.page-detail .price-wrap .price span.value {
  color: #EE4D2D;
  font-size: 1.5rem;
  vertical-align: bottom;
}
.page-detail .price-wrap .price span.original-value {
  color: #EE4D2D;
  font-size: 1rem;
  vertical-align: bottom;
  background-color: #FFF5E2;
  padding: 0.0625rem 0.15625rem;
  margin-left: 0.625rem;
}
.page-detail .price-wrap .price span.del-value {
  text-decoration: line-through;
  color: #999;
  vertical-align: bottom;
  display: inline-block;
  overflow: hidden;
  height: 0.75rem;
  line-height: 0.75rem;
  vertical-align: sub;
  margin-left: 0.625rem;
}
.page-detail .price-wrap .title {
  line-height: 1.5rem;
  color: #000;
  font-weight: 400;
  width: 100%;
  height: 100%;
  padding-bottom: 0.3125rem;
}
.page-detail .submit-button {
  height: 2.8125rem;
  margin: 0.625rem auto;
  background-color: #FFA41C;
  border: 0.03125rem solid #FF8F00;
  box-shadow: 0 0.0625rem 0.15625rem 0 #000;
  text-align: center;
  line-height: 2.8125rem;
  border-radius: 1.25rem;
  color: #000;
  font-size: 1.1875rem;
  font-weight: 400;
  cursor: pointer;
}
.page-detail a {
  color: #000;
}
.page-detail .rich-text-editor a {
  color: #06c;
}
